
import api from '@/api';
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import foot from '@/components/aashare/foot';
import hot from  '@/components/aashare/hotList';
import navSwipe from '../../../static/js/swipe.js';
import wx from '@/wx';
export default {
name: 'hello',
data () {
return {
slides:[],
categories:"",
campaigns:"",
/*text*/
text1:"",
text2:"",
hotParams:{
action:"Home/HotSales",
title:"部分精选"
}
}
},

created:function(){
api.ajaxLaoding('',
"Home/Index",{}
).then(res=>{
this.slides = res.data.result.slides.map( item => {
return {
url:"",
img:item.image,
title: ''
}
});
this.categories = res.data.result.categories;
this.campaigns = res.data.result.campaigns;
this.text1 = "热门活动";
this.text2="所有宝贝";
}).catch(()=>{
console.log("失败");
});
wx.share();
},

updated: function (){
var num = this.campaigns.length;
var box = document.querySelector("#box");
var wrap = document.querySelector("#wrap");
var Width = document.querySelectorAll(".wrap-item")[0].offsetWidth;
wrap.style.width = num*Width +3+"px";
navSwipe(box,wrap);
},

methods:{
goClassfiy(){
/*this.$router.push({path:"/classfiy"});*/
alert("即将上线")
},
goReach(){
this.$router.push({path:"/search"});
},
goSeek(){
this.$router.push({path:"/seek"});
},
goSearchlist(id,word){
this.$router.push({path:"/searchList",query:{cateId:id,cateword:word}});
},
goAcitve(id){

this.$router.push({path:"/activeDetail",query:{id:id}});
}
},

components: {
foot,
hot,
Swiper,
SwiperItem
}
}



