
import Divider from 'vux/src/components/divider/index.vue'

import api from '@/api';
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
name: '',
props:['params'],
data(){
return {
title:"",
hotData:"",
isBottom:false,
loading:false,
scroll:false,
page:1
}
},

created:function(){
api.ajaxLaodingN('',
this.params.action,{"pageIndex":this.page,"pageSize":6}
).then(res=>{
console.log(res);
this.hotData = res.data.result.products;
this.scroll = true;
this.page ++;
if(!res.data.result.hasNext){
this.isBottom = true;
this.scroll = false ;
}
}).catch(()=>{
console.log("失败");
})
},

methods:{
like(index,id){
api.ajax('',
"Goods/Like",{"id":id}
).then(res=>{
console.log(res);
if(res.data.result){
this.$vux.toast.show({
text: '点赞成功',
type:"text",
time:1000
});
this.hotData[index].hasLike = true;
this.hotData[index].likeCount ++;
}else {
this.$vux.toast.show({
text: '已经点过赞',
type:"text",
time:1000
});
}
}).catch(()=>{
console.log("失败");
});
},

loadMore(){
if(this.scroll == false){
return;
};
console.log("进入更多");
this.loading = true;
api.ajaxN('',
this.params.action,{"pageIndex":this.page,"pageSize":6}
).then(res=>{
var data = res.data.result.products;
this.page ++;
this.hotData.push(...data);
this.loading = false;
if(!res.data.result.hasNext){
this.isBottom = true;
this.scroll = false ;
}
}).catch(()=>{
console.log("失败");
})
}
},

components: {
Divider,
Spinner
}
}



